<template>
  <div>
    <div class="examine" v-show="tableShow">
      <div class="top">
        <h3>任务状态:</h3>
        <el-select v-model="value" placeholder="请选择" style="width: 100px;float: left;margin-top: 6px">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>

    
       

        <!-- <el-button style="margin-top: 6px;float: right;margin-right: 20px" type="primary">查询</el-button> -->
            <!-- <input class="search" placeholder="请输入任务标题" > -->
        <el-input  class="search" placeholder="请输入内容" @keyup.enter.native='search' v-model="searchs">
               <i slot="suffix" class="el-input__icon search-icon"  @click="search">
                   <img src="./../assets/img/search.png" alt="">
               </i>
        </el-input>
      </div>
      <el-table
        :data="tableData2"
        style="width: 100%;margin-top: 20px"
        :row-class-name="tableRowClassName" header-cell-class-name="tableHead" cell-class-name="tableCell">
        <el-table-column type="index" label="序号" width="100px">

        </el-table-column>
        <el-table-column
          prop="address"
          label="任务标题"
        >
        </el-table-column>
          <el-table-column
            prop="address"
            label="验收进度"
          >
        </el-table-column>
       
        <el-table-column
          prop="date"
          label="时间">
        </el-table-column>
        <el-table-column
          prop="address"
          label="操作">
          <template slot-scope="scope">
            <el-button type="primary" plain @click="showInfo(scope.$index, scope.row)">同意</el-button>
            <el-button type="danger"  plain @click="deleteInfo(scope.$index, scope.row)" >拒绝</el-button>

          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="1000" style="text-align: center;margin-top: 20px;">
      </el-pagination>

    </div>
   
  </div>
</template>

<script>
    export default {
        name: "approvalAcceptance",
      data(){
          return{
            value:'',
            options:[{value:'1',label:1}],
            tableData2: [{
                date: '2016-05-02',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄',
                }, {
                date: '2016-05-04',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
                }, {
                date: '2016-05-01',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄',
                }, {
                date: '2016-05-03',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
                }, {
                date: '2016-05-03',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
                }, {
                date: '2016-05-03',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
                }, {
                date: '2016-05-03',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
            }],
            tableShow:true,
            infoShow:false,
            searchs:'',//搜索信息
          }
      },
      methods:{
        //   搜索事件
          search(){
              console.log('handlclick')
          },
        tableRowClassName({row, rowIndex}) {
          if (rowIndex%2 == 0) {
            return '';
          } else{
            return 'cellBlue';
            
          }

        },
        showInfo(index,row){
          // this.tableShow=false;
          // this.infoShow=true
          this.$confirm('是否同意通过该项目审批？', '', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
          }).then(() => {
            this.$message({
              type: 'success',
              message: '同意成功!'
            });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消'
            });
          });
        },
        deleteInfo(index, row){
          console.log(1)
          this.$prompt('请输入拒绝原因', '拒绝原因', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            inputErrorMessage: '邮箱格式不正确'
          }).then(({ value }) => {
            this.$message({
              type: 'success',
              message: '拒绝成功'
            });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '取消输入'
            });
          });

        }

      }

    }
</script>

<style scoped lang='scss'>
  .content .progress{
    width: 100%;
    height: 20px;
    box-sizing: border-box;
    padding-left: 33px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .content .progress p{
    width: 343px;
    height: 20px;
    font-size:16px;
    font-family:PingFang-SC-Medium;
    font-weight:500;
    color:rgba(51,51,51,1);
    float: left;
    box-sizing: border-box;
    padding-left: 10px;
  }
  .content .progress span{
    font-size:16px;
    line-height: 20px;
    font-family:PingFang-SC-Medium;
    font-weight:500;
    color:rgba(153,153,153,1);
  }
  .content .progress h2{
    width: 20px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    background:rgba(37,54,145,1);
    border-radius:50%;
    font-size:16px;
    font-family:PingFang-SC-Medium;
    font-weight:500;
    color:rgba(255,255,255,1);
    float: left;
  }
  .content .btn{
    width:200px;
    height:60px;
    line-height: 60px;
    text-align: center;
    background:rgba(37,54,145,1);
    border-radius:8px;
    font-size:16px;
    font-family:PingFang-SC-Medium;
    font-weight:500;
    color:rgba(255,255,255,1);
    margin-left:31px;
    margin-top: 53px;
    margin-bottom: 20px;
  }
  .content h5{
    box-sizing: border-box;
    height: 28px;
    line-height: 28px;
    padding-left: 74px;
    font-size:20px;
    font-family:PingFang-SC-Medium;
    font-weight:bold;
    color:rgba(37,54,145,1);
    background-image: url("./../assets/img/exProgress.png");
    background-repeat: no-repeat;
    background-position: 32px;
    margin-top: 40px;
  }
  .content h3{
    font-size:20px;
    font-family:PingFang-SC-Medium;
    font-weight:bold;
    color:rgba(37,54,145,1);
    box-sizing: border-box;
    padding-left: 30px;
  }
  .content .text{
    width: 100%;
    height: 40px;
    line-height: 40px;
    margin-top: 10px;
  }
  .content .text img{
    width:200px;
    height:150px;
    float: left;
  }
  .content .text h4,.content .text p{

    height: 40px;
    font-size:16px;
    font-family:PingFang-SC-Medium;
    font-weight:500;
    color:rgba(51,51,51,1);
    float: left;
  }
  .content .text h4{
    width: 100px;
    text-align: right;
    margin-right: 23px;
  }
  
  .examine .top{
    width: 990px;
    height: 81px;
    box-sizing: border-box;
    padding-top: 31px;
  }
  .examine .top h3{
    font-size:16px;
    height: 50px;
    line-height: 50px;
    font-family:PingFang-SC-Medium;
    font-weight:500;
    color:rgba(51,51,51,1);
    float: left;
    margin-left: 20px;
    margin-right: 10px;
  }
  .examine .top .search{
    width:240px;
    height:40px;
    /* background:rgba(37,54,145,0);
    border:1px solid rgba(204,204,204,1);
    border-radius:8px; */
    float: right;
    margin-top: 6px;
    /* box-sizing: border-box;
    
    background-image: url("./../assets/img/search.png");
    background-repeat: no-repeat;
    background-position: 206px;
    margin-left: 20px;
    padding-left: 10px; */
  }
.search-icon{
    img{
        width: 24px;
        height: 24px;
        margin-top:8px; 
    }
}
</style>
<style>
  .el-table .tableHead{
    text-align: center;
    font-size:16px;
    font-family:PingFang-SC-Medium;
    font-weight:500;
    color:rgba(51,51,51,1);
    background-color: rgba(245,247,255,1)
  }
  .el-table .tableCell{
    text-align: center;
    font-size:16px;
    font-family:PingFang-SC-Medium;
    font-weight:500;
    color:rgba(51,51,51,1);
  }
  .el-table .cellBlue{
    background-color: rgba(245,247,255,1)
  }

</style>




